@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,700;1,400;1,800&display=swap');

.App {
  box-sizing: border-box;
  background-color: white;
  margin: 80px 16px 0;
  padding: 40px 24px;
  border-radius: 24px 24px 72px 24px;
  width: 100%;
  max-width: 600px;
}
.label-container {
  display: flex;
  gap: 16px;
}
.label-container label {
  max-width: 120px;
}
.label-container label span {
  color: hsl(0, 1%, 44%);
  font-size: .8em;
  letter-spacing: 2px;
  text-transform: uppercase;
}
.label-container.invalid label span {
  color: hsl(0, 100%, 67%);
}
p.invalid {
  color: hsl(0, 100%, 67%);
  font-size: .8em;
}
.label-container label input {
  color: hsl(0, 0%, 8%);
  font-size: 1.2em;
  font-family: 'Poppins', sans-serif;
  margin-top: 2px;
  padding: 12px 12px;
  box-sizing: border-box;
  width: 100%;
  outline: none;
  border: 1px solid hsl(0, 0%, 86%);
  border-radius: 8px;
}
.App form .button-container {
  position: relative;
  padding: 32px 0;
  display: flex;
  justify-content: center;
  border-bottom: 2px solid hsl(0, 0%, 86%);
}
.App form .button-container button {
  background-color: hsl(259, 100%, 65%);
  outline: none;
  border: none;
  border-radius: 50%;
  height: 64px;
  width: 64px;
  position: absolute;
  cursor: pointer;
}
.App form .button-container button:hover {
  background-color: hsl(0, 0%, 8%);
}
.App form .button-container button img {
  height: 28px;
  width: 28px;
}

.App .content {
  margin-top: 64px;
}
.App .content p {
  line-height: 1;
  font-family: 'Poppins';
  font-style: italic;
  font-weight: 800;
  font-size: 48px;
  margin: 0;
}
.App .content p span {
  color: hsl(259, 100%, 65%);
}
.App .author {
  margin: 64px auto 0;
  text-align: center;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

@media screen and (min-width: 670px) {
  .App {
    margin: 100px auto 0;
  }  
  .App form .button-container {
    justify-content: flex-end;
  }
  .App .content p {
    font-size: 72px;
  }
}